* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  text-decoration: none;
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

input, button {
  border: none;
  outline: none;
}

body {
  letter-spacing: .02rem;
  color: #fefefe;
  background-color: #101217;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif;
  font-weight: 500;
  line-height: 1.2;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

.main {
  gap: 30px;
  max-width: 1470px;
  margin: 0 auto;
  padding: 15px;
  display: flex;
}

.students {
  background-color: #181b23;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.students--table {
  flex-grow: 1;
  max-width: 1050px;
}

.students__title {
  font-size: 24px;
}

.students__table {
  border-collapse: collapse;
  table-layout: fixed;
  inline-size: 100%;
  max-width: 1050px;
}

.students__th {
  background-color: #121319;
  padding: 5px;
}

.students__tr, .students__td {
  text-align: center;
  word-break: break-word;
  border: 1px solid #dedede;
  padding: 8px;
  font-size: 18px;
}

.students__tr {
  cursor: pointer;
  transition: background-color .3s;
}

.students__tr.is-edit .students__edit {
  display: block;
}

.students__tr.is-edit .students__view {
  display: none;
}

.students__tr:hover {
  background-color: #222732;
}

.students__edit {
  color: #fefefe;
  background-color: #1a1c22;
  border: 1px solid #ffffff1f;
  border-radius: 8px;
  max-width: 125px;
  padding: 8px;
  font-size: 16px;
  display: none;
}

.students__edit--delete, .students__edit--save {
  color: #ff5c7c;
  cursor: pointer;
  border: 1px solid #ff5c7c;
  transition: background-color .3s;
}

.students__edit--delete:hover, .students__edit--save:hover {
  background-color: #392227;
}

.students__edit--save {
  color: #2ad47a;
  border: 1px solid #2ad47a;
}

.students__edit--save:hover {
  background-color: #30453a;
}

.students__edit:not(.students__edit--delete, .students__edit--save):focus {
  border-color: #fff3;
}

.students__btn {
  cursor: pointer;
  color: #fefefe;
  background-color: #6b719b;
  border-radius: 8px;
  max-width: 250px;
  padding: 12px 16px;
  font-size: 18px;
  transition: background-color .3s;
}

.students__btn:hover {
  background-color: #787eae;
}

.students__btn--submit {
  margin-top: 30px;
}

.students--form {
  gap: 30px;
}

.students__form {
  inline-size: 100%;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  max-width: 400px;
  display: flex;
}

.students__label {
  color: #c8c8c8;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  inline-size: 100%;
  display: flex;
  position: relative;
}

.students__label--isEnrolled {
  background-color: #0000;
  border: none;
  flex-direction: row;
  justify-content: space-between;
}

.students__input {
  color: #fefefe;
  background-color: #121319;
  background-color: inherit;
  border: 1px solid #2b2e39;
  border-radius: 8px;
  padding: 10px;
  font-size: 18px;
  transition: border-color .3s;
}

.students__input:focus {
  border-color: #3f4455;
}
